<template>
  <div class="addGoods">
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>

    <div class="content">
      <el-alert
        title=""
        type="info"
        center
        :closable="false"
        close-text
        show-icon
      >
      </el-alert>

      <div class="buzhou">
        <el-steps
          :active="active"
          finish-status="success"
          :space="200"
          align-center
        >
          <el-step title=""></el-step>
          <el-step title=""></el-step>
          <el-step title=""></el-step>
          <el-step title=""></el-step>
          <el-step title=""></el-step>
          <el-step title=""></el-step>
        </el-steps>

        <el-tabs :tab-position="tabPosition" @tab-click="activeNum">
          <el-tab-pane label="">
            <el-form
              :model="addGoods"
              ref="addGoods"
              :rules="rules"
              :label-position="labelPosition"
              label-width="80px"
            >
              <el-form-item prop="goods_name" label="">
                <el-input v-model="addGoods.goods_name"></el-input>
              </el-form-item>
              <el-form-item prop="goods_price" label="">
                <el-input v-model="addGoods.goods_price"></el-input>
              </el-form-item>
              <el-form-item prop="goods_number" label="">
                <el-input v-model="addGoods.goods_number"></el-input>
              </el-form-item>
              <el-form-item prop="goods_weight" label="">
                <el-input v-model="addGoods.goods_weight"></el-input>
              </el-form-item>
              <el-form-item prop="goods_weight" label="">
                  <el-cascader
                    v-model="addGoods.goods_cat"
                    :options="categoriesList"
                    :props="peizhi"
                    clearable
                  ></el-cascader>
              </el-form-item>
            </el-form>
          </el-tab-pane>
          <el-tab-pane label=""></el-tab-pane>
          <el-tab-pane label=""></el-tab-pane>
          <el-tab-pane label=""></el-tab-pane>
          <el-tab-pane label="">
            <div class="textarea">
               <textarea  name="" id="" cols="30" rows="10"></textarea>
            <el-button style="margin-top:20px;" @click="add" type="primary"></el-button>
            </div>
           
          </el-tab-pane>
        </el-tabs>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
    
      categoriesList:[],
       peizhi: {
        label: "cat_name",
        value: "cat_id",
        children: "children",
        expandTrigger: "hover",
      },
      active: 1,
      tabPosition: "left",
      labelPosition: "top",
      rules: {
        goods_name: [
          { required: true, message: "", trigger: "blur" },
        ],
        goods_price: [
          { required: true, message: "", trigger: "blur" },
        ],
        goods_weight: [
          { required: true, message: "", trigger: "blur" },
        ],
        goods_number: [
          { required: true, message: "", trigger: "blur" },
        ],
        goods_fenlei: [
          { required: true, message: "", trigger: "blur" },
        ],
      },
      addGoods: {
        goods_name: "",
        goods_number: "",
        goods_price: "",
        goods_weight: "",
        goods_introduce:"",
        goods_cat:[]
      },
    };
  },
  created() {},
  mounted( ){
    this.getcategories()
  },
  
  methods: {

    // 
   async add(){
     this.addGoods.goods_cat=this.addGoods.goods_cat.join(",")

     let {data}=await this.$http.post("/goods",this.addGoods)

     this.$router.push("/home/goods")
     console.log(data)

    },

    // 
    async getcategories() {
      let { data } = await this.$http.get("/categories", {
        params: { type: 3 },
      });

      console.log(data);
      this.categoriesList = data.data;
    },

    // 
    activeNum(i) {
      this.active = i.paneName;
      console.log(this.active);
    },
  },
};
</script>

<style lang="scss" scoped>
.textarea{

  display: flex;
  flex-direction: column;

}
.el-tabs {
  margin-top: 30px;
}
.el-tab-pane {
  width: 100%;

  height: 1000px;
}
.content {
  width: 100%;
  margin-top: 20px;
  height: 1000px;
  background: white;
  padding: 30px;
  > p {
    width: 100%;
    height: 40px;
  }
}
.buzhou {
  width: 100%;

  margin-top: 30px;
}
</style>
